@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto:wght@300&display=swap');


.insurance {
    width: 100%;
    height: 70vh;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background: rgba(236, 232, 232, 0.144);
  }
  
  .insurance:before {
    content: '';
    position: absolute;
    background: url('../../assets/menu-bg-insurance.webp') no-repeat center top/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
  }


.insurance .insurance-content {
    display: block;
    position: absolute;
    bottom: 10%;
    padding: 1rem;
}

.insurance h1 {
    font-size: 4rem;
    font-family: 'Montserrat', sans-serif;
    color: rgb(241, 247, 247);
}



/* MEDIA QUERIES */

@media screen and (max-width:940px) {


.insurance h1 {
    font-size: 3rem;
    text-align: center;
}

.insurance .insurance-content {
    width: 100%;
}
}